                <Heading class="mb-1">Select Switches</Heading>
                <div class="flex flex-wrap gap-3">
                    <div v-for="n in getRange(1, 13)" :key="n"
                        :class="{ 'ring ring-blue-400': options.content_switch_style === n, }"
                        @click.prevent="setFromRange(n, 'content_switch_style');"
                        class="flex flex-col items-center justify-center transition duration-75 bg-gray-100 rounded-lg cursor-pointer w-36 h-36">
                    <DarkModeSwitch :id="n"/>
                    </div>
                </div>
            </div>
        </Card>
        
        <Suggestions paths="shortcode, excludes, accessibility"></Suggestions>
    </Card>
